<template>
    <div :class="itemClasses">
        <div :class="headerClasses"
            @click="toggle"
            :draggable="draggable"
            @dragstart="handleDragstart($event, index)"
            @dragover.prevent
            @drop="handleDrop($event, index)">
            <Icon type="arrow" v-if="!hideArrow"></Icon>
            <slot></slot>
        </div>
        <collapse-transition v-if="mounted">
            <div :class="contentClasses" v-show="isActive">
                <div :class="boxClasses"><slot name="content"></slot></div>
            </div>
        </collapse-transition>
    </div>
</template>
<script>
    import Icon from '../icon/icon.vue';
    import CollapseTransition from '../base/collapse-transition';
    const prefixCls = 'ov-collapse';
    export default {
        name: 'Panel',
        components: { Icon, CollapseTransition },
        props: {
            name: {
                type: String
            },
            hideArrow: {
                type: Boolean,
                default: false
            }
        },
        data () {
            return {
                index: 0, // use index for default when name is null
                isActive: false,
                mounted: false
            };
        },
        computed: {
            itemClasses () {
                return [
                    `${prefixCls}-item`,
                    {
                        [`${prefixCls}-item-active`]: this.isActive
                    }
                ];
            },
            headerClasses () {
                return `${prefixCls}-header`;
            },
            contentClasses () {
                return `${prefixCls}-content`;
            },
            boxClasses () {
                return `${prefixCls}-content-box`;
            }
        },
        methods: {
            toggle () {
                this.$parent.toggle({
                    name: this.name || this.index,
                    isActive: this.isActive
                });
            },
            handleDragstart (event, index) {
              // 将被拖拽元素的index存入dataTransfer
              event.dataTransfer.setData('dragIndex', index)
            },
            handleDrop (event, index) {
              // 被拖拽的元素index - dragIndex
              const dragIndex = event.dataTransfer.getData('dragIndex')
              this.$parent.handleDrop(dragIndex, index)
            }
        },
        mounted () {
            this.mounted = true;
        }
    };
</script>